import * as actionTypes from '../helpers/constants';
import {getTime} from "date-fns";
import ReceiptPaperRollsApi from '../api/actual/ReceiptPaperRollsApi';


export const loadReceiptPaperRolls = (token, params = {}) => dispatch => {
    dispatch({type: actionTypes.LOAD_RECEIPT_PAPER_ROLLS});

    return ReceiptPaperRollsApi
        .list(token, params)
        .then(({data: rolls}) => {
            dispatch({type: actionTypes.LOAD_RECEIPT_PAPER_ROLLS_SUCCESSFUL, rolls});
        })
        .catch(error => {
            dispatch({type: actionTypes.LOAD_RECEIPT_PAPER_ROLLS_FAILED});
            console.log(error);});
};

export const addReceiptPaperRoll = (token, data) => dispatch => {
    dispatch({type: actionTypes.ADD_RECEIPT_PAPER_ROLL});

    return ReceiptPaperRollsApi
        .add(token, data)
        .then(({data: {created}}) => {
            if(created){
                dispatch({type: actionTypes.ADD_RECEIPT_PAPER_ROLL_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.ADD_RECEIPT_PAPER_ROLL,
                    timestamp: getTime(Date()),
                    data: {created}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.ADD_RECEIPT_PAPER_ROLL,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.ADD_RECEIPT_PAPER_ROLL_FAILED});
            console.log(error);});
};


