import * as actionTypes from '../helpers/constants';
import {getTime} from "date-fns";
import RibbonsApi from '../api/actual/RibbonsApi';


export const loadRibbons = (token, params = {}) => dispatch => {
    dispatch({type: actionTypes.LOAD_RIBBONS});

    return RibbonsApi
        .list(token, params)
        .then(({data: ribbons}) => {
            dispatch({type: actionTypes.LOAD_RIBBONS_SUCCESSFUL, ribbons});
        })
        .catch(error => {
            dispatch({type: actionTypes.LOAD_RIBBONS_FAILED});
            console.log(error);});
};

export const addRibbon = (token, data) => dispatch => {
    dispatch({type: actionTypes.ADD_RIBBON});

    return RibbonsApi
        .add(token, data)
        .then(({data: {created}}) => {
            if(created){
                dispatch({type: actionTypes.ADD_RIBBON_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.ADD_RIBBON,
                    timestamp: getTime(Date()),
                    data: {created}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.ADD_RIBBON,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.ADD_RIBBON_FAILED});
            console.log(error);});
};


