import React from 'react';
import {Button, Intent, NumericInput, Radio, RadioGroup, Text, Popover, PopoverInteractionKind, Position, ButtonGroup} from "@blueprintjs/core";
import {DateInput, TimePrecision} from "@blueprintjs/datetime";
import { bindActionCreators } from 'redux';
import {connect} from 'react-redux';
import {loadRibbons, addRibbon} from '../../actions/Ribbons';
import _isEqual from 'lodash/isEqual';
import _last from 'lodash/last';
import _first from 'lodash/first';
import _isEmpty from 'lodash/isEmpty';
import {isFormValid} from "../../helpers";
import format from 'date-fns/format';
import Toaster from "../Common/Toaster";

class Ribbons extends React.Component{

    state = {
        amount: 0,
        type: 1,
        date: new Date(),
        inStock: 0,
        received: 0,
        confirmCommit: false
    };

    componentDidMount(){
        const {authUser: {token}, loadRibbons} = this.props;
        loadRibbons(token, {});
    }

    static getDerivedStateFromProps(nextProps, prevState){
        const {inStock: prevInStock, received: prevReceived} = prevState;
        const {ribbonTransactions} = nextProps;

        const nextInStock = _isEmpty(ribbonTransactions) ? 0 : _last(ribbonTransactions).stock_after_transaction;
        const nextReceived = _isEmpty(ribbonTransactions) ? 0 : _first(ribbonTransactions).stock_after_transaction;

        if(_isEqual(nextInStock, prevInStock) && _isEqual(nextReceived, prevReceived)){
            return null;
        }

        return {
            inStock: nextInStock,
            received: nextReceived
        }
    }

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, loadRibbons, authUser: {token}} = this.props;

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            loadRibbons(token, {});

            Toaster.show({
                message: `Transaction Successfully Comitted 😃`,
                intent: Intent.SUCCESS,
                icon: 'tick' });
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            Toaster.show({
                message: `Could Not Commit transaction 😞`,
                intent: Intent.DANGER,
                icon: 'error'
            });
        }
    }

    _handleInputChange = (name, value) => {
        this.setState(() => {
            return {
                [name]: value
            };
        })
    };

    _handleCommit = () => {
        const {authUser: {token}, addRibbon} = this.props;
        const {type, amount, date, inStock} = this.state;

        const afterTransaction = _isEqual(Number.parseInt(type, 10), 1) ? (inStock + amount) : (inStock - amount)

        let data = new FormData();
        data.append("transaction_type", type);
        data.append("amount", amount);
        data.append("date", format(date, "X"));
        data.append("stock_after_transaction", afterTransaction);
        data.append("stock_before_transaction", inStock);

        addRibbon(token, data);

        this.setState({confirmCommit: false, amount: 0, date: new Date(), type: 1});
    };

    _isFormValid(){
        const {amount, type, date} = this.state;

        let required = {amount, type, date: format(date, "X")};

        return isFormValid(required);
    }

    render(){
        const {amount, type, date, inStock, received} = this.state;
        const disableSaveButton = !this._isFormValid();

        return (
            <div className={"Ribbon_Tab"}>
                <div className="Status">
                    {/* <div className="Received">
                        <h4 className="Header"><Text ellipsize={true}>Received</Text></h4>
                        <p className="Value">
                            {received}
                        </p>
                    </div> */}
                    <div className="Left">
                        <h4 className="Header"><Text ellipsize={true}>In Stock</Text></h4>
                        <p className="Value">
                            {inStock}
                        </p>
                    </div>
                </div>
                <div className="New_Transaction">
                    <div className="Header">New Transaction</div>
                    <section className="Content">
                        <form action="">
                            <label className="bp3-label">
                                {/*Amount*/}
                                <NumericInput
                                    max={this.state.inStock}
                                    clampValueOnBlur={this.state.inStock > 0 && Number.parseInt(this.state.type, 10) === 0}
                                    buttonPosition={"none"}
                                    allowNumericCharactersOnly
                                    onValueChange={(valueNumber, valueString) => this._handleInputChange("amount", valueNumber)}
                                    placeholder="Enter number of ribbons"
                                    value={amount}
                                    />
                            </label>
                            <label className="bp3-label">

                                <RadioGroup
                                    name={"type"}
                                    inline={true}
                                    label="Transaction Type"
                                    onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                    selectedValue={type}
                                >
                                    <Radio label="Check In" value="1" />
                                    <Radio label="Check Out" value="0"  disabled={_isEqual(this.state.inStock, 0)}/>
                                </RadioGroup>
                            </label>
                            <label className="bp3-label">
                                {/*Date*/}
                                <DateInput
                                    placeholder={"Choose transaction date"}
                                    timePrecision={TimePrecision.MINUTE}
                                    formatDate={date => date.toLocaleString()}
                                    parseDate={str => new Date(str)}/>
                            </label>
                            <ButtonGroup>
                                <Popover isOpen={this.state.confirmCommit} disabled={disableSaveButton} interactionKind={PopoverInteractionKind.CLICK} position={Position.BOTTOM}>
                                    <Button
                                        loading={this.props.addingRibbonTransaction}
                                        onClick={() => this.setState({confirmCommit: true})}
                                        disabled={disableSaveButton}
                                        fill
                                        intent={Intent.PRIMARY}
                                        icon="tick"
                                        text="commit"
                                    />
                                    <div style={{ padding: ".5rem", display: "flex", flexDirection: "column" }}>
                                        <header>You are about to commit a transaction which is irreversible. Are you sure you want to continue?</header>
                                        <div style={{ display: "flex", justifyContent: "space-evenly", marginTop: ".5rem" }}>
                                            <Button onClick={this._handleCommit} small text="yes" intent={Intent.SUCCESS}/>
                                            <Button onClick={() => this.setState({confirmCommit: false})} small text="no" intent={Intent.DANGER}/>
                                        </div>
                                    </div>
                                </Popover>
                            </ButtonGroup>
                        </form>
                    </section>
                </div>
            </div>
        );
    }

}

const mapStateToProps = ({authUser, addingRibbonTransaction, ribbonTransactions, OPERATION_FAILED, OPERATION_SUCCESSFUL}) => (
    {addingRibbonTransaction, ribbonTransactions, OPERATION_FAILED, OPERATION_SUCCESSFUL, authUser});
const mapDispatchToProps = dispatch => bindActionCreators({loadRibbons, addRibbon}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Ribbons);


