import * as actionTypes from '../helpers/constants';
import {getTime} from "date-fns";
import LaminatesApi from '../api/actual/LaminatesApi';


export const loadLaminates = (token, params = {}) => dispatch => {
    dispatch({type: actionTypes.LOAD_LAMINATES});

    return LaminatesApi
        .list(token, params)
        .then(({data: laminates}) => {
            dispatch({type: actionTypes.LOAD_LAMINATES_SUCCESSFUL, laminates});
        })
        .catch(error => {
            dispatch({type: actionTypes.LOAD_LAMINATES_FAILED});
            console.log(error);});
};

export const addLaminate = (token, data) => dispatch => {
    dispatch({type: actionTypes.ADD_LAMINATE});

    return LaminatesApi
        .add(token, data)
        .then(({data: {created}}) => {
            if(created){
                dispatch({type: actionTypes.ADD_LAMINATE_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.ADD_LAMINATE,
                    timestamp: getTime(Date()),
                    data: {created}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.ADD_LAMINATE,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.ADD_LAMINATE_FAILED});
            console.log(error);});
};


