import * as actionTypes from '../helpers/constants';
import {getTime} from "date-fns";
import CardsApi from '../api/actual/CardsApi';


export const loadCards = (token, params = {}) => dispatch => {
    dispatch({type: actionTypes.LOAD_CARDS});

    return CardsApi
        .list(token, params)
        .then(({data: cards}) => {
            dispatch({type: actionTypes.LOAD_CARDS_SUCCESSFUL, cards});
        })
        .catch(error => {
            dispatch({type: actionTypes.LOAD_CARDS_FAILED});
            console.log(error);});
};

export const addCard = (token, data) => dispatch => {
    dispatch({type: actionTypes.ADD_CARD});

    return CardsApi
        .add(token, data)
        .then(({data: {created}}) => {
            if(created){
                dispatch({type: actionTypes.ADD_CARD_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.ADD_CARD,
                    timestamp: getTime(Date()),
                    data: {created}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.ADD_CARD,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.ADD_CARD_FAILED});
            console.log(error);});
};


